import React from 'react';
import ItemCard from '../components/order/orderHistory'
import CheckOut from '../components/order/checkOutForm'
import Grid from '@material-ui/core/Grid';



export default class Account extends React.Component {
  constructor(props) {
    super(props);
    
    }
  render() {
    return (
      <div>
      <Grid container>
      <Grid item xs={7}>
      <CheckOut/> 
       </Grid>

      <Grid item xs={5}> 
      <ItemCard/>
       </Grid>
      </Grid>
    	</div>




      );
  }
}
