import React from 'react';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import './company.css';
import indomie from '../../images/indomie.jpg'
import Divider from '@material-ui/core/Divider';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import green from '@material-ui/core/colors/green';
import Star from '@material-ui/icons/Star';
import Chip from '@material-ui/core/Chip';



const theme = createMuiTheme({
  palette: {
    primary: green,
  },
 
});

function Header(props) {
 

  return (
    <div >
      <Grid className="company-background" container>
       
        <h1 className="page-heading"> Discover the best donuts </h1>
       
        </Grid>

      <Grid style={{marginTop:'50px', marginBottom: '50px', marginLeft: '7%', width:'80%'}} container >
        

        <Grid item xs={6}>
        <h2 className="company-name"> Krispy Donuts </h2>
          <h6 className="company-name">We are Africa's Largest donut shop that ships the best donuts at the best prices </h6>
      
        <p className="rating-open"> Open Hours: 12:00PM - 9:30PM </p>
        
        
        <p className="rating-open"> Rating: <Star className="star"/> <Star className="star"/> <Star className="star"/> </p>
        </Grid>

        <Grid item xs={6}>
        
         <MuiThemeProvider theme={theme}>
          <Chip className="chip" 
          color="primary"
          label="Fast Food"  
          variant="outlined" />
          </MuiThemeProvider>
       
        </Grid>
      
      </Grid>
       <Divider style={{width: '85%', marginLeft:'7%'}}/>
    </div>
  );
}
export default Header;
