import React from 'react';
import Grid from '@material-ui/core/Grid';
import Featured from '../components/dashboard/featured'
import Food from '../components/dashboard/food'
import Travel from '../components/dashboard/travel'
import Tours from '../components/dashboard/tours'
import SearchBar from 'material-ui-search-bar'
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

const styles = theme => ({
       


    dashboardHeading: {
    
    [theme.breakpoints.down('sm')]: {
     
    textAlign: 'center',
    color: '#FFFFFF',
    fontSize: '25px',
    fontStyle: 'bold',
    fontFamily: 'Josefin Sans',
    marginTop: '50px',

     

    },
    [theme.breakpoints.up('md')]: {
      
       textAlign: 'center',
    color: '#FFFFFF',
    fontSize: '35px',
    fontStyle: 'bold',
    fontFamily: 'Josefin Sans',
    marginTop: '400px',
    },
    [theme.breakpoints.up('lg')]: {
     
    textAlign: 'center',
    color: '#FFFFFF',
    fontSize: '35px',
    fontStyle: 'bold',
    fontFamily: 'Josefin Sans',
    marginTop: '400px',
    },
  },

});



class Dashboard extends React.Component {
  
  render() {
    const { classes } = this.props;
    return (
      <div>

      <Grid className="dashboard-hero"container>

      <Grid item xs={12}>
       
       <h2 className={classes.dashboardHeading}> Search for the best things around you</h2>

       <SearchBar
      onChange={() => console.log('onChange')}
      onRequestSearch={() => console.log('onRequestSearch')}
      style={{
        marginTop: '100px',
        margin: '0 auto',
        maxWidth: 300,
        borderRadius: '50px'
      }}
    />
     </Grid>
      
      </Grid>

      <h2 style={{marginTop: '20px', textAlign: 'center', position: 'relative'}}><b> Coming soon </b></h2>
        
       
      
    	</div>




      );
  }
}
export default withStyles(styles)(Dashboard)