//test layout for the featured section, to be continued

import React, {Component} from "react";
import Slider from "react-slick";
import indomie from '../../images/indomie.jpg';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Icon from '@material-ui/core/Icon';
import Grid from '@material-ui/core/Grid';
import { FaAngleRight, FaAngleLeft } from 'react-icons/fa';
import './dashboard.css'


function NextArrow(props) {
    const { className, onClick } = props
  return (
    
      <button onClick={props.onClick}  type="button"  className={className}
      style={{backgroundColor: '#1A5276', borderRadius: '50%', height: '50px', width: '50px'}}>
      <FaAngleRight style={{marginLeft: '4px',marginTop: '1px', color:"white", marginBottom: '1px', width: '40px', height: '40px'}} /> 
    </button>
  
  );
}

function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
     <button onClick={props.onClick}  type="button"  className={className}
      style={{backgroundColor: '#1A5276', borderRadius: '50%', height: '50px', width: '50px'}}>

       <FaAngleLeft style={{marginLeft: '0px',marginTop: '1px', color:"white", marginBottom: '1px', width: '40px', height: '40px'}} /> 
    </button>
  );
}

 

class Featured extends Component {
  render() {
     const settings = {
       nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
      infinite: false,
      dots: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true
            
            
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    };
    return (
      

      
      <Grid container className="grey-container" >

      <div style={{width: '80%',height: '100px', margin: '50px', marginLeft: '120px'}}>
        <h2 className="slide-text">Featured</h2>
        <Slider {...settings}>
          <div>
            <img style={{margin:'40px',width: '70%',height: '70%'}} src={indomie} />
             </div>
          <div>
            <img style={{margin:'40px',width: '70%',height: '70%'}} src={indomie} />
          </div>
          <div>
             <img style={{margin:'40px',width: '70%',height: '70%'}} src={indomie} />
          </div>
          <div>
             <img style={{margin:'40px', width: '70%',height: '70%'}} src={indomie} />
          </div>
          <div>
           <img style={{margin:'40px',width: '70%',height: '70%'}} src={indomie} /> 
          </div>
          <div>
            <img style={{margin:'40px', width: '70%',height: '70%'}} src={indomie} />
          </div>
        </Slider>
      </div>
      </Grid>
    );
  }
}
export default Featured;