import React, { Component } from 'react';
import picture from '../../images/cake.jpg'
import backchat from '../../images/chatback.jpg'
import Pusher from 'pusher-js';
import './apps.css';
import InputBase from '@material-ui/core/InputBase';
import SendIcon from '@material-ui/icons/Send';
import BackIcon from '@material-ui/icons/ArrowBack';
import IconButton from '@material-ui/core/IconButton';
import Avatar from '@material-ui/core/Avatar';
import Grid from '@material-ui/core/Grid';
import {  FaEllipsisH } from 'react-icons/fa';
import { withStyles, createMuiTheme } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import FreeScrollBar from 'react-free-scrollbar';
import 'react-chat-widget/lib/styles.css'
import { Widget, addResponseMessage } from 'react-chat-widget';


const styles = theme => ({


    form: {
    
    [theme.breakpoints.down(340)]: {
     
       
          width: '77%'
     

    },

      [theme.breakpoints.up(350)]: {
     
       
         width: '80%'
         
     

    },

    [theme.breakpoints.up(400)]: {
      
        width: '80%'
    },

  },

    chatBox: {
    
    [theme.breakpoints.down(340)]: {
     
       
          width: '100%',
           height: '310px',
             backgroundImage: `url(${backchat})`,
             backgroundRepeat: 'no-repeat',
       backgroundSize: 'cover',
         
     

    },

      [theme.breakpoints.up(350)]: {
     
       
          width: '100%',
           height: '380px',
           backgroundImage: `url(${backchat})`,
           backgroundRepeat: 'no-repeat',
       backgroundSize: 'cover',    
   
    },

    [theme.breakpoints.up(370)]: {
     
       
          width: '100%',
           height: '390px',
           backgroundImage: `url(${backchat})`,
           backgroundRepeat: 'no-repeat',
       backgroundSize: 'cover',    
   
    },


    [theme.breakpoints.up(400)]: {
      
        width: '100%',
           height: '470px',
           backgroundImage: `url(${backchat})`,
    },

  },


    chatWindow: {
    
    [theme.breakpoints.down('sm')]: {
     
       
          width: '100%',
          marginTop: '12%',
          height: '100%',
          margin: 'auto',
          border: '1px solid #d5d5d5',
         
     

    },
    [theme.breakpoints.up('md')]: {
      
       width: '500px',
          margin: 'auto',
          border: '1px solid #d5d5d5',
          marginBottom: '40px',
    },
    [theme.breakpoints.up('lg')]: {
     
      width: '500px',
      margin: 'auto',
      border: '1px solid #d5d5d5',
      marginBottom: '40px',
    },
  },

    arrow: {
    
    [theme.breakpoints.down('sm')]: {
     
       
          color: 'white',
           marginTop: '13px',
     

    },
    [theme.breakpoints.up('md')]: {
      
      marginLeft: '10px',
       color: 'white',
        marginTop: '8px'
    },
    [theme.breakpoints.up('lg')]: {
     
     marginLeft: '10px',
      color: 'white', 
      marginTop: '8px'
    },
  },

     dots: {
    
    [theme.breakpoints.down('sm')]: {
     
         marginLeft: '80px', 
         color: 'white',
          marginTop: '25px',

     

    },
    [theme.breakpoints.up('md')]: {
      
       marginLeft: '170px',
        color: 'white',
         marginTop: '27px',
    },
    [theme.breakpoints.up('lg')]: {
     
      marginLeft: '170px',
       color: 'white',
        marginTop: '25px',
    },
  },

    avatar: {
    
    [theme.breakpoints.down('sm')]: {
     
         marginLeft: '10px', 
         color: 'white',
        marginTop: '80%',
         

     

    },
    [theme.breakpoints.up('md')]: {
      
       marginLeft: '10px',
        color: 'white',
         marginTop: '18px'
    },
    [theme.breakpoints.up('lg')]: {
     
      marginLeft: '10px',
       color: 'white',
        marginTop: '15px'
    },
  },


    headerText: {
    
    [theme.breakpoints.down('sm')]: {
     
         fontFamily: 'Josefin Sans', 
         marginTop: '15%',
        marginLeft: '40px',

     

    },
    [theme.breakpoints.up('md')]: {
      
       fontFamily: 'Josefin Sans', 
         marginTop: '18px',
        marginLeft: '25px',
    },
    [theme.breakpoints.up('lg')]: {
     
       fontFamily: 'Josefin Sans', 
         marginTop: '17px',
        marginLeft: '25px',
    },
  },

    SendIconButton: {
    
    [theme.breakpoints.down('sm')]: {
     
         marginLeft: '15px',
         background: '#34495E',
          color: 'white',

     

    },
    [theme.breakpoints.up('md')]: {
      
       marginLeft: '40px',
        background: '#2E86C1', 
        color: 'white',
    },
    [theme.breakpoints.up('lg')]: {
     
       marginLeft: '40px',
        background: '#2E86C1',
         color: 'white',
    },
  },

});

const theme = createMuiTheme({
  breakpoints: {
    values: ["xs", "sm", "md", "lg", "xl", "xxl"]
  },
  values: {
    xs: 330,
    sm: 350,
    md: 390,
    lg: 400,
    xl: 500,
    xxl: 600
  }
});



class Chat extends Component {
      constructor(props) {
        super(props);
        this.state = {
          userMessage: '',
          conversation: [],
        };
      }


      componentDidMount() {
        const pusher = new Pusher('ebc3524c710d32ede099', {
          cluster: 'eu',
          encrypted: true,
        });

        const channel = pusher.subscribe('bot');
        channel.bind('bot-response', data => {
          const msg = {
            text: data.message,
            user: 'ai',
          };
          this.setState({
            conversation: [...this.state.conversation, msg],
          });
           });
      }

      handleChange = event => {
        this.setState({ userMessage: event.target.value });
      };

      handleSubmit = event => {
        event.preventDefault();
        if (!this.state.userMessage.trim()) return;

        const msg = {
          text: this.state.userMessage,
          user: 'human',
        };

        this.setState({
          conversation: [...this.state.conversation, msg],
        });

        fetch('https://kynectai.herokuapp.com/chat', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            message: this.state.userMessage,
          }),
        });

        this.setState({ userMessage: '' });
      };

      render() {

        const { classes } = this.props;

        const ChatBubble = (text, i, className) => {
          return (
         
            <Grid key={`${className}-${i}`} className={`${className} chat-bubble`}>
             
              <div className="chat-content">{text}</div>
              
            </Grid>
          
          );
        };

        const chat = this.state.conversation.map((e, index) =>
          ChatBubble(e.text, index, e.user)
        );

        return (
          <Grid container>

            
            <Grid className={classes.chatWindow}>
            
             <Grid container className="header">

              

             <Grid item xs={1}>
            <Avatar className={classes.avatar} src={picture} />
            </Grid>
            <Grid item xs={6}>
            <p className={classes.headerText} >Andrew <br/> online </p>
            
            </Grid>
           <Grid item xs={5}>
            
           </Grid>
              </Grid>
               <div className={classes.chatBox}>
            <FreeScrollBar>

              <div className="conversation-view">{chat}</div>
             </FreeScrollBar>
            </div>
              <div className="message-box">


                <form onSubmit={this.handleSubmit}>
         
        <InputBase 
        className={classes.form}
         value={this.state.userMessage}
        onInput={this.handleChange}
        type="text"
        placeholder="Type your message and hit Enter to send"/>
        

          
         <IconButton type='submit' className={classes.SendIconButton} aria-label="Send" >
          <SendIcon />
        </IconButton>
                
                </form>

              </div>

             
            </Grid>
          </Grid>
        );
      }
    }

Chat.propTypes = {
  classes: PropTypes.object.isRequired,
};




export default withStyles(styles)(Chat);